@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
}

.home-container {
  padding: 20vh 18vw;
  line-height: 50px;
}

.home-title {
  font-size: 2.5em;
}
.home-hello {
  color: #edb879;
}
.home-button {
  color: black;
  background: #edb879;
  border: #042f66 solid;
  padding: 15px;
  margin-right: 10px;
  transition: background 0.5s;
  font-size: 16px;
  text-decoration: none;
}

.home-button:hover {
  background: white;
  transition: background 0.5s;
}

.about-columns {
  display: flex;
  justify-content: space-between;
  margin: 0 10%;
  padding: 6vw 0;
}

.about-intro {
  font-style: italic;
}
.about-columns > div {
  width: 45%;
  line-height: 28px;
}

.about-name {
  font-size: 40px;
  line-height: 50px;
  color: #edb879;
}
.about-img {
  width: 100%;
  border-radius: 5%;
}

.about-list > li:not(:last-child) {
  margin-bottom: 10px;
}

.about-list > li {
  list-style: none;
  padding-left: 20px;
  line-height: 20px;
}
.about-title {
  text-align: center;
  margin-bottom: 10px;
  color: #edb879;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
}

.form-container {
  padding: 20vh 18vw;
  line-height: 30px;
}
.form-header {
  font-size: 2.5rem;
  color: #edb879;
}

.form-content {
  font-size: 23px;
  padding: 10px 0;
}

.form-submit {
  color: black;
  background: #edb879;
  border: #042f66 solid;
  padding: 15px;
  margin-right: 10px;
  transition: background 0.5s;
  font-size: 16px;
  text-decoration: none;
}
.form-submit:hover {
  background: white;
  transition: background 0.5s;
}
.form-input {
  width: 100%;
  line-height: 25px;
  border-radius: 5px;
  border: none;
  margin-bottom: 14px;
  margin-top: 8px;
  padding: 8px 10px;
  color: black;
  font-size: 20px;
}

.footer {
  position: relative;
  z-index: 100;
  width: 80%;
  margin: auto;
  color: #fff;
  padding: 0.75em;
  border-top: #aaaaaa 1px solid;
}

.container {
  color: #fff;
  padding: 0;
  display: flex;
  text-decoration: none;
  justify-content: center;
  list-style: none;
  text-align: center;
}
.footer_content ul {
  list-style: none;
  display: flex;
  text-align: center;
}
.footer_content a {
  color: #fff;
  text-decoration: none;
  padding: 0 0.5rem;
  text-align: center;
}

.footer_content a:hover {
  color: #edb879;
}

.navbar {
  position: relative;
  width: 100%;
  height: 64px;
  font-family: "Nanum Myeongjo", serif;
  z-index: 100;
  box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.5);
}

.navbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.navbar__logo {
  margin-left: 1rem;
}

.navbar__logo a {
  color: #fff;
  text-decoration: none;
  font-size: 1.5rem;
}

.spacer {
  flex: 1 1;
}

.navbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.navbar_navigation-items li {
  padding: 0 0.5rem;
}

.navbar_navigation-items a {
  color: #fff;
  text-decoration: none;
}

.navbar_navigation-items a:hover,
.navbar_navigation-items a:active {
  color: #edb879;
  text-decoration: underline;
}
.navbar__logo a:hover,
.navbar__logo a:active {
  color: #edb879;
}

@media (max-width: 768px) {
  .navbar_navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .navbar__toggle-button {
    display: none;
  }
  .navbar__logo {
    margin-left: 0;
  }
}

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button__line {
  width: 30px;
  height: 2px;
  background: #fff;
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.project-container {
  display: flex;
  position: absolute;
  width: 100%;
  top: 0;
}
.project {
  width: 25%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.project a {
  text-decoration: none;
  font-size: 30px;
}

.project-img {
  position: absolute;
  overflow: hidden;
  width: 25%;
  top: 0;
  bottom: 0;
  opacity: 0;
  transition: ease-out 0.5s;
}

.project-img:hover {
  opacity: 0.3;
  transition: ease-in 0.5s;
  color: #fff;
}

.project-img-1 {
  background: url(/static/media/project-1.a254fc68.jpeg);
}
.project-img-2 {
  background: url(/static/media/project-2.fc83f028.jpeg);
}
.project-img-3 {
  background: url(/static/media/project-3.838461ed.jpeg);
}
.project-img-4 {
  background: url(/static/media/project-4.18c76f2c.jpeg);
}

.project-text {
  position: relative;
  z-index: 200;
  color: #edb879;
}

.project-text:hover {
  color: #fff;
}

.project-bio {
  margin-top: 8px;
}

@media (max-width: 767px) {
  .project-container {
    flex-direction: column;
    position: static;
  }
  .project {
    width: 100%;
    position: relative;
    height: 40vh;
  }
  .project-img {
    width: 100%;
    opacity: 0.3;
  }
}

.side-drawer {
  height: 100vh;
  background-color: #fff;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 400;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.side-drawer li {
  margin: 2rem 0;
}

.side-drawer a {
  text-decoration: none;
  color: #195e83;
  font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: #edb879;
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
  top: 0;
  left: 0;
}

* {
  font-family: "Nanum Myeongjo", serif;
  box-sizing: border-box;
}

.app {
  background: linear-gradient(335deg, #195e83 10%, #042f66 50%, #195e83 100%);
}

.main-content {
  min-height: calc(100vh - 108px);
}

